/* ----------------------------------
   .sec2:   
---------------------------------- */
.sec2 {
  padding: 1em;
  box-sizing: border-box;
}

/* ----------------------------------
   .sec2-title:   
---------------------------------- */
.sec2-title {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 10%;
  padding: 1em;
  box-sizing: border-box;
}

/* .sec2-title    */
.sec2-title h2 {
  color: ivory;
  opacity: 0.5;
  font-size: 4rem;
  letter-spacing: 8px;
}

/* ----------------------------------
   .skill-title:   
---------------------------------- */
.skill-title {
  width: 100%;
  height: 50px;
  line-height: 50px;
  text-align: center;
  color: ivory;
  font-size: 25px;
  font-weight: 700;
  letter-spacing: -1px;
  border-radius: 20px;
}

/* ----------------------------------
    (1024px  )
---------------------------------- */
@media (max-width: 1024px) {
  .sec2 {
    height: 100%;
  }
  .sec2-title h2 {
    font-size: 3rem;
    letter-spacing: 2px;
  }
}
