/* ----------------------------------
   .sec4-title:   
---------------------------------- */
.sec4 {
  width: 100%;
  height: 100vh;
}

.sec4-title {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 120px;
  padding: 1em;
  box-sizing: border-box;
}

/* .sec4-title    */
.sec4-title h2 {
  color: ivory;
  opacity: 0.5;
  font-size: 4rem;
  letter-spacing: 2px;
}

/* ----------------------------------
   .sec4-box:   
---------------------------------- */
.sec4-box {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  height: 50%;
  margin-top: 50px;
  padding: 1rem;
  gap: 50px;
  color: #444;
  font-size: 20px;
  font-weight: 900;
  text-align: center;
  box-sizing: border-box;
}

/* ----------------------------------
   .contact-item:   
---------------------------------- */
.contact-item {
  width: calc((100% - 50px) / 2); /*  : clac  calc */
  height: 100%;
}

/*       */
.contact-item a {
  display: block;
  width: 100%;
  height: 100%;
}

/*    +  */
.contact-item a img {
  width: 300px;
  height: 300px;
  animation: bounce3 1s infinite alternate;
}

/*    */
.contact-item-desc {
  margin-top: 10px;
  font-size: 25px;
  color: ivory;
}

/* ----------------------------------
   bounce3  
---------------------------------- */
@keyframes bounce3 {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(15px);
  }
}

/* ----------------------------------
   :   1024px  
---------------------------------- */
@media (max-width: 1024px) {
  .sec4-box {
    display: block;
  }
  .contact-item {
    width: 100%;
    height: 100%;
  }
  .contact-item a img {
    width: 200px;
    height: auto;
  }
  .sec4-title h2 {
    font-size: 3rem;
  }
}
