/* ------------------------------
   .values:   
------------------------------ */
.values {
  background: ivory;
}

/* ------------------------------
   .values-box:   
------------------------------ */
.values-box {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  overflow: hidden;
}

/* ------------------------------
   .values-title:   
------------------------------ */
.values-title {
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 120px;
}

/* .values-title    */
.values-title h2 {
  color: rgb(37, 37, 43);
  margin: 0px;
  text-align: center;
  font-size: 4.5em;
  opacity: 0.5;
}

/* ------------------------------
   .values-L:   (  )
------------------------------ */

.values-L {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  height: 100%;
  padding: 1em;
  box-sizing: border-box;
}

/* ------------------------------
   .values-R:   ( )
------------------------------ */

.values-R {
  width: 50%;
  height: 100%;
  background: url(../img/background/main2.jpg) center/cover no-repeat;
}

/* ------------------------------
   .values-text:  
------------------------------ */
.values-text {
  width: 100%;
  height: 100%;
  display: grid;
  align-items: center;
  justify-items: center;
  /*  2,   1:1 */
  grid-template-columns: 1fr 1fr;
  /*  2,   100px, auto */
  grid-template-rows: auto auto;
  grid-gap: 10px; /*    */
  color: #25252b;
}

.values-item {
  width: 90%;
  height: 90%;
  border-radius: 50%;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  color: ivory;
  font-size: 1.8rem;
  background-color: #60606f;
}

/* ------------------------------
     (1024px )
------------------------------ */
@media (max-width: 1024px) {
  /*     */
  .values-box {
    flex-direction: column;
  }

  /*        */
  .values-L,
  .values-R {
    width: 100%;
    height: auto;
  }

  /*     */
  .values-text {
    display: flex;
    flex-direction: column;
  }

  .values-item {
    width: 280px;
    height: auto;
    padding: 20px 30px;
    font-size: 1rem;
  }
  /*     */
  .values-title {
    height: 60px;
  }

  .values-title h2 {
    font-size: 2rem;
  }
}
