/*     */
.zoom-in {
  transform: scale(1.3);
  transition: transform 0.4s ease;
}

.zoom-out {
  transform: scale(1);
  transition: transform 0.4s ease;
}

.container {
  width: 100%; /*   100%      */
  height: 60%; /*   800px  */
  display: flex; /*     flex   */
  justify-content: center; /*       */
  align-items: center; /*       */
  position: relative; /*    (relative)   */
  transform: rotateX(-10deg); /*  X  -10  */
  transform-style: preserve-3d; /* 3D       */
  perspective: 2000px; /* (3D)  2000px  */
}

.card {
  max-width: 200px; /*    200px  */
  position: absolute; /*        */
  backface-visibility: visible; /*      */
  transition: transform 1s ease-in-out; /* (, )  1   */
}
.card img {
  width: 100%;
}

@media (max-width: 1024px) {
  .sec2 {
    overflow: hidden;
  }
  .container {
    height: 80vh; /*     */
    perspective: 1500px; /* 3D  */
    transform: rotateX(-5deg); /* X  */
  }
  .card {
    max-width: 100px; /*     */
    height: auto; /*     */
  }
}
