/* ------------------------------
   .sec1:   
------------------------------ */
.sec1 {
  background: ivory;
}

/* ------------------------------
   .sec1_box:   
------------------------------ */
.sec1_box {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  overflow: hidden;
}

/* ------------------------------
   .sec1-title:   
------------------------------ */
.sec1-title {
  align-items: center;
  justify-content: center;
}

/* .sec1-title    */
.sec1-title h2 {
  color: rgb(37, 37, 43);
  margin-bottom: 2rem;
  text-align: center;
  font-size: 4.5em;
  opacity: 0.5;
}

/* ------------------------------
   .sec1_L:   (  )
------------------------------ */
.sec1_L {
  width: 50%;
  height: 100%;
  background: url(../img/background/main3.jpg) center/cover no-repeat;
}

/* ------------------------------
   .sec1_R:   ( )
------------------------------ */
.sec1_R {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  height: 100%;
  padding: 1em;
  box-sizing: border-box;
}

/* ------------------------------
   .about-text:  
------------------------------ */
.about-text {
  text-align: center;
  font-size: 30px;
  color: #25252b;
}

/* ------------------------------
     (1024px )
------------------------------ */
@media (max-width: 1024px) {
  /*     */
  .sec1_box {
    flex-direction: column;
  }

  /*        */
  .sec1_L,
  .sec1_R {
    width: 100%;
    height: 50%;
  }
  /*     */
  .about-text {
    font-size: 1.2rem;
  }
  .sec1-title h2 {
    font-size: 2rem;
  }
}
